  // Preguntas Frecuentes - Acordeón
document.addEventListener('DOMContentLoaded', function() {
    const faqItems = document.querySelectorAll('.faq-item');
    
    faqItems.forEach(item => {
        const pregunta = item.querySelector('.faq-pregunta');
        const respuesta = item.querySelector('.faq-respuesta');
        const icono = item.querySelector('.faq-pregunta i');
        
        // Ocultar todas las respuestas al inicio
        respuesta.style.display = 'none';
        
        pregunta.addEventListener('click', function() {
            const isActive = item.classList.contains('active');
            
            // Cerrar todos los elementos FAQ
            faqItems.forEach(otherItem => {
                otherItem.classList.remove('active');
                otherItem.querySelector('.faq-respuesta').style.display = 'none';
                otherItem.querySelector('.faq-pregunta i').style.transform = 'rotate(0deg)';
            });
            
            // Si no estaba activo, abrirlo
            if (!isActive) {
                item.classList.add('active');
                respuesta.style.display = 'block';
                icono.style.transform = 'rotate(180deg)';
            }
        });
    });
});  