document.addEventListener('DOMContentLoaded', function() {
    // =============================================
    // FILTRADO DE PROYECTOS
    // =============================================
    const filtroBtns = document.querySelectorAll('.filtro-btn');
    const tarjetasProyecto = document.querySelectorAll('.tarjeta-proyecto');
    
    if (filtroBtns.length && tarjetasProyecto.length) {
        filtroBtns.forEach(btn => {
            btn.addEventListener('click', function() {
                // Quitar activo de todos los botones
                filtroBtns.forEach(b => b.classList.remove('activo'));
                // Añadir activo al clickeado
                this.classList.add('activo');
                
                const filtro = this.dataset.filtro;
                
                // Filtrar proyectos
                tarjetasProyecto.forEach(tarjeta => {
                    tarjeta.style.display = (filtro === 'todos' || tarjeta.dataset.categoria === filtro) 
                        ? 'block' 
                        : 'none';
                });
                
                // Actualizar el botón "Ver más" después del filtrado
                actualizarBotonVerMas(filtro);
            });
        });
    }

    // =============================================
    // GALERÍA DE IMÁGENES (Lightbox básico)
    // =============================================
    const imagenesGaleria = document.querySelectorAll('.imagen-item img');
    
    imagenesGaleria.forEach(img => {
        img.addEventListener('click', function() {
            // Crear modal
            const modal = document.createElement('div');
            modal.style.cssText = `
                position: fixed;
                top: 0;
                left: 0;
                width: 100vw;
                height: 100vh;
                background: rgba(15, 35, 70, 0.95);
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 1000;
                cursor: zoom-out;
            `;
            
            // Crear imagen ampliada
            const imagenAmpliada = new Image();
            imagenAmpliada.src = this.src;
            imagenAmpliada.style.maxHeight = '90vh';
            imagenAmpliada.style.maxWidth = '90vw';
            imagenAmpliada.style.objectFit = 'contain';
            imagenAmpliada.style.borderRadius = '8px';
            
            // Botón de cerrar
            const closeBtn = document.createElement('span');
            closeBtn.innerHTML = '&times;';
            closeBtn.style.cssText = `
                position: absolute;
                top: 30px;
                right: 30px;
                color: white;
                font-size: 40px;
                cursor: pointer;
            `;
            
            modal.appendChild(imagenAmpliada);
            modal.appendChild(closeBtn);
            
            // Cerrar al hacer click
            const cerrarModal = () => document.body.removeChild(modal);
            modal.addEventListener('click', cerrarModal);
            closeBtn.addEventListener('click', cerrarModal);
            
            document.body.appendChild(modal);
        });
    });

    // =============================================
    // ANIMACIONES AL HACER SCROLL
    // =============================================
    const elementosAnimar = document.querySelectorAll('.tarjeta-proyecto, .paso-proceso, .titulo-seccion');
    
    const animarAlScroll = () => {
        elementosAnimar.forEach(el => {
            const rect = el.getBoundingClientRect();
            const isVisible = (rect.top <= window.innerHeight * 0.8);
            
            if (isVisible) {
                el.style.opacity = '1';
                el.style.transform = 'translateY(0)';
            }
        });
    };
    
    // Aplicar estilos iniciales
    elementosAnimar.forEach(el => {
        el.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
        el.style.opacity = '0';
        el.style.transform = 'translateY(30px)';
    });
    
    // Ejecutar al cargar y al hacer scroll
    window.addEventListener('load', animarAlScroll);
    window.addEventListener('scroll', animarAlScroll);

    // =============================================
    // FUNCIONALIDAD "VER MÁS PROYECTOS"
    // =============================================
    
    // Variables para el control de "Ver más"
    const btnVerMas = document.getElementById('btnVerMas');
    let mostrandoTodos = false;
    let filtroActual = 'todos';
    
    // Función para actualizar el botón "Ver más"
    function actualizarBotonVerMas(categoria = 'todos') {
        filtroActual = categoria;
        
        // Contar proyectos ocultos que coinciden con el filtro actual
        const proyectosOcultos = Array.from(tarjetasProyecto).filter(proyecto => {
            const categoriaProyecto = proyecto.getAttribute('data-categoria');
            const esVisible = categoria === 'todos' || categoriaProyecto === categoria;
            const esOculto = proyecto.classList.contains('proyecto-oculto');
            
            return esVisible && esOculto && !mostrandoTodos;
        });
        
        if (btnVerMas) {
            if (proyectosOcultos.length > 0 && !mostrandoTodos) {
                btnVerMas.style.display = 'inline-block';
                btnVerMas.innerHTML = '<i class="fas fa-plus-circle"></i> Ver Más Proyectos';
            } else if (mostrandoTodos) {
                btnVerMas.innerHTML = '<i class="fas fa-minus-circle"></i> Ver Menos';
                btnVerMas.style.display = 'inline-block';
            } else {
                btnVerMas.style.display = 'none';
            }
        }
    }
    
    // Función para aplicar filtro completo
    function aplicarFiltroCompleto(categoria) {
        filtroActual = categoria;
        
        tarjetasProyecto.forEach(proyecto => {
            const categoriaProyecto = proyecto.getAttribute('data-categoria');
            const esVisible = categoria === 'todos' || categoriaProyecto === categoria;
            const esOculto = proyecto.classList.contains('proyecto-oculto');
            
            if (esVisible) {
                // Si debe mostrarse y no está oculto por "Ver más"
                if (!esOculto || mostrandoTodos) {
                    proyecto.style.display = 'block';
                } else {
                    proyecto.style.display = 'none';
                }
            } else {
                proyecto.style.display = 'none';
            }
        });
        
        // Actualizar botón "Ver más"
        actualizarBotonVerMas(categoria);
    }
    
    // Event listener para botón "Ver más"
    if (btnVerMas) {
        btnVerMas.addEventListener('click', function() {
            if (!mostrandoTodos) {
                // MOSTRAR proyectos ocultos
                mostrandoTodos = true;
                
                tarjetasProyecto.forEach(proyecto => {
                    const categoriaProyecto = proyecto.getAttribute('data-categoria');
                    const esVisible = filtroActual === 'todos' || categoriaProyecto === filtroActual;
                    
                    if (esVisible && proyecto.classList.contains('proyecto-oculto')) {
                        proyecto.style.display = 'block';
                    }
                });
                
                this.innerHTML = '<i class="fas fa-minus-circle"></i> Ver Menos';
                
            } else {
                // OCULTAR proyectos nuevamente
                mostrandoTodos = false;
                
                tarjetasProyecto.forEach(proyecto => {
                    if (proyecto.classList.contains('proyecto-oculto')) {
                        proyecto.style.display = 'none';
                    }
                });
                
                // Reaplicar filtro actual
                aplicarFiltroCompleto(filtroActual);
            }
        });
    }
    
    // Modificar los event listeners de los filtros para usar la nueva función
    if (filtroBtns.length && tarjetasProyecto.length) {
        filtroBtns.forEach(btn => {
            btn.addEventListener('click', function() {
                // Quitar activo de todos los botones
                filtroBtns.forEach(b => b.classList.remove('activo'));
                // Añadir activo al clickeado
                this.classList.add('activo');
                
                const filtro = this.dataset.filtro;
                
                // Resetear estado de "Ver más" al cambiar filtro
                mostrandoTodos = false;
                
                // Aplicar filtro completo
                aplicarFiltroCompleto(filtro);
            });
        });
    }
    
    // Inicializar al cargar la página
    actualizarBotonVerMas('todos');
});
