/**
 * ================================
 * FLOTANTES.JS - BOMBAS AYALA LIGHT
 * Versión ligera y funcional
 * ================================
 */

class FlotantesSimple {
    constructor() {
        this.flotantesContainer = document.querySelector('.flotantes');
        this.flotantes = document.querySelectorAll('.flotante');
        
        this.isVisible = true;
        this.lastScrollTop = 0;
        
        this.init();
    }

    init() {
        if (!this.flotantesContainer) return;

        this.setupScrollBehavior();
        this.setupClickEffects();
        this.setupTooltips();
        
        console.log('💫 Flotantes Simple iniciado');
    }

    // ==================
    // SCROLL BEHAVIOR
    // ==================
    
    setupScrollBehavior() {
        let scrollTimer;
        
        window.addEventListener('scroll', () => {
            const currentScroll = window.pageYOffset;
            const scrollDirection = currentScroll > this.lastScrollTop ? 'down' : 'up';
            
            // Ocultar al bajar, mostrar al subir
            if (currentScroll > 200) {
                if (scrollDirection === 'down') {
                    this.hide();
                } else if (scrollDirection === 'up') {
                    this.show();
                }
            } else {
                this.show();
            }
            
            this.lastScrollTop = currentScroll;
        }, { passive: true });
    }

    // ==================
    // EFECTOS DE CLICK
    // ==================
    
    setupClickEffects() {
        this.flotantes.forEach((flotante) => {
            flotante.addEventListener('click', (e) => {
                this.addClickEffect(flotante);
                this.trackClick(flotante);
            });
        });
    }

    addClickEffect(flotante) {
        flotante.style.transform = 'scale(0.9)';
        
        setTimeout(() => {
            flotante.style.transform = '';
        }, 150);
        
        // Vibración en móviles
        if ('vibrate' in navigator) {
            navigator.vibrate(50);
        }
    }

    trackClick(flotante) {
        const type = flotante.classList.contains('whatsapp') ? 'WhatsApp' :
                    flotante.classList.contains('llamada') ? 'Llamada' : 'Instagram';
        
        console.log(`📱 Click en ${type}`);
        
        // Google Analytics si existe
        if (typeof gtag !== 'undefined') {
            gtag('event', 'flotante_click', {
                'flotante_type': type
            });
        }
    }

    // ==================
    // TOOLTIPS SIMPLES
    // ==================
    
    setupTooltips() {
        const tooltips = [
            'Chatea por WhatsApp',
            'Llámanos ahora',
            'Síguenos en Instagram'
        ];
        
        this.flotantes.forEach((flotante, index) => {
            flotante.setAttribute('data-tooltip', tooltips[index]);
        });
    }

    // ==================
    // CONTROL DE VISIBILIDAD
    // ==================
    
    show() {
        if (this.isVisible) return;
        
        this.flotantesContainer.style.opacity = '1';
        this.flotantesContainer.style.visibility = 'visible';
        this.flotantesContainer.style.transform = 'translateY(-50%) translateX(0)';
        
        this.isVisible = true;
    }

    hide() {
        if (!this.isVisible) return;
        
        this.flotantesContainer.style.opacity = '0';
        this.flotantesContainer.style.visibility = 'hidden';
        this.flotantesContainer.style.transform = 'translateY(-50%) translateX(50px)';
        
        this.isVisible = false;
    }

    // ==================
    // API PÚBLICA
    // ==================
    
    forceShow() { this.show(); }
    forceHide() { this.hide(); }
    toggle() { this.isVisible ? this.hide() : this.show(); }
}

// ==================
// INICIALIZACIÓN
// ==================

document.addEventListener('DOMContentLoaded', function() {
    window.flotantesController = new FlotantesSimple();
    
    // API global simple
    window.Flotantes = {
        show: () => window.flotantesController?.forceShow(),
        hide: () => window.flotantesController?.forceHide(),
        toggle: () => window.flotantesController?.toggle()
    };
});

// ==================
// WHATSAPP CON MENSAJE
// ==================

document.addEventListener('DOMContentLoaded', function() {
    const whatsappBtn = document.querySelector('.flotante.whatsapp');
    
    if (whatsappBtn) {
        whatsappBtn.addEventListener('click', function(e) {
            e.preventDefault();
            
            const message = encodeURIComponent(
                'Hola! Me interesa conocer más sobre sus servicios de bombas de agua. 💧'
            );
            
            window.open(`https://wa.me/51997984102?text=${message}`, '_blank');
        });
    }
});