// ===============================
// FORMULARIO DE COTIZACIÓN
// ===============================
document.addEventListener('DOMContentLoaded', function() {
    const formulario = document.getElementById('formularioCotizacion');
    
    if (formulario) {
        formulario.addEventListener('submit', function(e) {
            e.preventDefault();
            
            // Obtener datos del formulario
            const formData = new FormData(formulario);
            const datos = {
                nombre: formData.get('nombre'),
                telefono: formData.get('telefono'),
                email: formData.get('email'),
                servicio: formData.get('servicio'),
                descripcion: formData.get('descripcion')
            };
            
            // Validar campos requeridos
            if (!datos.nombre || !datos.telefono || !datos.servicio) {
                alert('Por favor, completa todos los campos obligatorios.');
                return;
            }
            
            // Construir mensaje para WhatsApp
            let mensaje = `🔧 *SOLICITUD DE COTIZACIÓN*\n\n`;
            mensaje += `👤 *Cliente:* ${datos.nombre}\n`;
            mensaje += `📞 *Teléfono:* ${datos.telefono}\n`;
            if (datos.email) {
                mensaje += `📧 *Email:* ${datos.email}\n`;
            }
            mensaje += `🛠️ *Servicio:* ${datos.servicio}\n`;
            if (datos.descripcion) {
                mensaje += `📝 *Descripción:* ${datos.descripcion}\n`;
            }
            mensaje += `\n¡Espero su cotización!`;
            
            // Codificar mensaje para URL
            const mensajeCodificado = encodeURIComponent(mensaje);
            
            // Redirigir a WhatsApp
            const urlWhatsApp = `https://wa.me/51997984102?text=${mensajeCodificado}`;
            window.open(urlWhatsApp, '_blank');
            
            // Opcional: Redirigir a contactos.html después de un momento
            setTimeout(() => {
                window.location.href = 'contactos.html';
            }, 2000);
            
            // Mostrar mensaje de confirmación
            alert('¡Gracias! Te estamos redirigiendo a WhatsApp para enviarnos tu solicitud.');
            
            // Limpiar formulario
            formulario.reset();
        });
    }
});

// ===============================
// ANIMACIONES DE SCROLL
// ===============================

// Intersection Observer para animaciones
const observerOptions = {
    threshold: 0.1,
    rootMargin: '0px 0px -50px 0px'
};

const observer = new IntersectionObserver(function(entries) {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            entry.target.style.opacity = '1';
            entry.target.style.transform = 'translateY(0)';
        }
    });
}, observerOptions);

// Aplicar animaciones a las tarjetas de servicios
document.addEventListener('DOMContentLoaded', function() {
    const cards = document.querySelectorAll('.servicio-card, .testimonio-card');
    
    cards.forEach(card => {
        card.style.opacity = '0';
        card.style.transform = 'translateY(30px)';
        card.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
        observer.observe(card);
    });
});

// ===============================
// CONTADOR ANIMADO PARA ESTADÍSTICAS
// ===============================

function animateCounter(element, start, end, duration) {
    let startTime = null;
    
    function animation(currentTime) {
        if (startTime === null) startTime = currentTime;
        const timeElapsed = currentTime - startTime;
        const progress = Math.min(timeElapsed / duration, 1);
        
        const value = Math.floor(progress * (end - start) + start);
        element.textContent = end.toString().includes('%') ? value + '%' : 
                                end.toString().includes('+') ? value + '+' : 
                                end.toString().includes('/') ? '24/7' : value;
        
        if (progress < 1) {
            requestAnimationFrame(animation);
        }
    }
    
    requestAnimationFrame(animation);
}

// Observar estadísticas del hero
const statsObserver = new IntersectionObserver(function(entries) {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            const numero = entry.target.querySelector('.stat-numero');
            const texto = numero.textContent;
            
            if (texto.includes('5000+')) {
                animateCounter(numero, 0, 5000, 2000);
            } else if (texto.includes('98%')) {
                animateCounter(numero, 0, 98, 2000);
            } else if (texto.includes('24/7')) {
                numero.textContent = '24/7';
            }
            
            statsObserver.unobserve(entry.target);
        }
    });
}, { threshold: 0.5 });

document.addEventListener('DOMContentLoaded', function() {
    const statItems = document.querySelectorAll('.stat-item');
    statItems.forEach(item => {
        statsObserver.observe(item);
    });
});

// ===============================
// SMOOTH SCROLL PARA ENLACES INTERNOS
// ===============================

document.addEventListener('DOMContentLoaded', function() {
    const enlaces = document.querySelectorAll('a[href^="#"]');
    
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            
            const targetId = this.getAttribute('href');
            const targetElement = document.querySelector(targetId);
            
            if (targetElement) {
                targetElement.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        });
    });
});

// ===============================
// TRACKING DE EVENTOS PARA ANALÍTICAS
// ===============================

function trackEvent(action, category, label) {
    // Aquí puedes integrar con Google Analytics u otra herramienta
    console.log(`Evento: ${action}, Categoría: ${category}, Etiqueta: ${label}`);
}

document.addEventListener('DOMContentLoaded', function() {
    // Tracking de clics en botones de cotización
    const botonesCotizar = document.querySelectorAll('.boton-cotizar');
    botonesCotizar.forEach(boton => {
        boton.addEventListener('click', function() {
            const servicio = this.closest('.servicio-card').querySelector('.servicio-titulo').textContent;
            trackEvent('Click', 'Cotización', servicio);
        });
    });

    // Tracking de clics en botones de emergencia
    const botonesEmergencia = document.querySelectorAll('.boton-emergencia');
    botonesEmergencia.forEach(boton => {
        boton.addEventListener('click', function() {
            const tipo = this.classList.contains('whatsapp') ? 'WhatsApp' : 'Llamada';
            trackEvent('Click', 'Emergencia', tipo);
        });
    });

    // Tracking de clics en elementos flotantes
    const flotantes = document.querySelectorAll('.flotante');
    flotantes.forEach(flotante => {
        flotante.addEventListener('click', function() {
            const tipo = this.classList.contains('whatsapp') ? 'WhatsApp' : 
                        this.classList.contains('llamada') ? 'Llamada' : 'Instagram';
            trackEvent('Click', 'Flotante', tipo);
        });
    });
});

// ===============================
// LAZY LOADING PARA IMÁGENES
// ===============================

document.addEventListener('DOMContentLoaded', function() {
    const imagenes = document.querySelectorAll('img[data-src]');
    
    const imageObserver = new IntersectionObserver(function(entries) {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                const img = entry.target;
                img.src = img.dataset.src;
                img.classList.remove('lazy');
                imageObserver.unobserve(img);
            }
        });
    });

    imagenes.forEach(img => {
        imageObserver.observe(img);
    });
});

// ===============================
// VALIDACIÓN EN TIEMPO REAL DEL FORMULARIO
// ===============================

document.addEventListener('DOMContentLoaded', function() {
    const inputs = document.querySelectorAll('#formularioCotizacion input, #formularioCotizacion select');
    
    inputs.forEach(input => {
        input.addEventListener('blur', function() {
            validateField(this);
        });
        
        input.addEventListener('input', function() {
            if (this.classList.contains('error')) {
                validateField(this);
            }
        });
    });
});

function validateField(field) {
    const value = field.value.trim();
    const isRequired = field.hasAttribute('required');
    
    // Remover clases de error previas
    field.classList.remove('error');
    const errorMsg = field.parentNode.querySelector('.error-message');
    if (errorMsg) {
        errorMsg.remove();
    }
    
    // Validar campo requerido
    if (isRequired && !value) {
        showFieldError(field, 'Este campo es obligatorio');
        return false;
    }
    
    // Validar email
    if (field.type === 'email' && value) {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(value)) {
            showFieldError(field, 'Ingresa un email válido');
            return false;
        }
    }
    
    // Validar teléfono
    if (field.type === 'tel' && value) {
        const phoneRegex = /^[0-9]{9,}$/;
        if (!phoneRegex.test(value.replace(/\s+/g, ''))) {
            showFieldError(field, 'Ingresa un teléfono válido (mínimo 9 dígitos)');
            return false;
        }
    }
    
    return true;
}

function showFieldError(field, message) {
    field.classList.add('error');
    
    const errorDiv = document.createElement('div');
    errorDiv.className = 'error-message';
    errorDiv.textContent = message;
    errorDiv.style.color = '#E74C3C';
    errorDiv.style.fontSize = '0.85rem';
    errorDiv.style.marginTop = '0.25rem';
    
    field.parentNode.appendChild(errorDiv);
}

// ===============================
// MEJORAS DE ACCESIBILIDAD
// ===============================

document.addEventListener('DOMContentLoaded', function() {
    // Agregar atributos de accesibilidad
    const botones = document.querySelectorAll('button, .boton-cotizar, .boton-emergencia, .flotante');
    
    botones.forEach(boton => {
        if (!boton.getAttribute('aria-label') && !boton.textContent.trim()) {
            const icono = boton.querySelector('i');
            if (icono) {
                const clases = icono.className;
                if (clases.includes('whatsapp')) {
                    boton.setAttribute('aria-label', 'Contactar por WhatsApp');
                } else if (clases.includes('phone')) {
                    boton.setAttribute('aria-label', 'Llamar por teléfono');
                } else if (clases.includes('instagram')) {
                    boton.setAttribute('aria-label', 'Seguir en Instagram');
                }
            }
        }
    });
});

// ===============================
// DETECCIÓN DE DISPOSITIVO MÓVIL
// ===============================

function isMobile() {
    return window.innerWidth <= 768;
}

function adjustForMobile() {
    if (isMobile()) {
        // Ajustes específicos para móvil
        const flotantes = document.querySelector('.flotantes');
        if (flotantes) {
            flotantes.style.position = 'fixed';
            flotantes.style.bottom = '20px';
            flotantes.style.right = '20px';
            flotantes.style.top = 'auto';
            flotantes.style.transform = 'none';
            flotantes.style.flexDirection = 'row';
            flotantes.style.gap = '10px';
        }
    }
}

document.addEventListener('DOMContentLoaded', adjustForMobile);
window.addEventListener('resize', adjustForMobile);