// Array de imágenes de fondo que se irán rotando
const backgroundImages = [
    '/build/img/descargados/inicio/foto_1_1980.webp',
    '/build/img/descargados/inicio/foto_2_1980.webp',
    '/build/img/descargados/inicio/foto_3_1980.webp',
    '/build/img/descargados/inicio/foto_4_1980.webp'
];

// Array de imágenes principales que cambiarán
const mainImages = [
    '/build/img/descargados/inicio/foto_1_500.webp',
    '/build/img/descargados/inicio/foto_2_500.webp',
    '/build/img/descargados/inicio/foto_3_500.webp',
    '/build/img/descargados/inicio/foto_4_500.webp'
];

let currentImageIndex = 0;
const heroBackground = document.getElementById('heroBackground');
const heroImage = document.getElementById('heroImage');
const loadingIndicator = document.getElementById('loadingIndicator');

// Función para cambiar imagen de fondo
function changeBackgroundImage() {
    currentImageIndex = (currentImageIndex + 1) % backgroundImages.length;
    
    heroBackground.style.backgroundImage = `url('${backgroundImages[currentImageIndex]}')`;
    
    // También cambiar la imagen principal
    heroImage.src = mainImages[currentImageIndex];
}

// Función de inicialización
function initializeHero() {
    // Establecer primera imagen
    heroBackground.style.backgroundImage = `url('${backgroundImages[0]}')`;
    heroImage.src = mainImages[0];
    
    // Ocultar indicador de carga después de un momento
    setTimeout(() => {
        loadingIndicator.classList.add('hidden');
    }, 1500);

    // Cambiar imágenes cada 5 segundos
    setInterval(changeBackgroundImage, 5000);
}

// Función para smooth scroll en los enlaces
function initializeSmoothScroll() {
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.addEventListener('click', function (e) {
            e.preventDefault();
            const target = document.querySelector(this.getAttribute('href'));
            if (target) {
                target.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        });
    });
}

// Función para detectar cuando el elemento está en viewport
function initializeScrollAnimations() {
    const observerOptions = {
        threshold: 0.1,
        rootMargin: '0px 0px -50px 0px'
    };

    const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.classList.add('animate-in');
            }
        });
    }, observerOptions);

    // Observar elementos animables
    document.querySelectorAll('.hero-content, .hero-image').forEach(el => {
        observer.observe(el);
    });
}

// Función para precargar imágenes
function preloadImages() {
    [...backgroundImages, ...mainImages].forEach(src => {
        const img = new Image();
        img.src = src;
    });
}

// Inicializar todo cuando el DOM esté listo
document.addEventListener('DOMContentLoaded', function() {
    preloadImages();
    initializeHero();
    initializeSmoothScroll();
    initializeScrollAnimations();
});

// Optimización de rendimiento: pausar animaciones cuando no están visibles
document.addEventListener('visibilitychange', function() {
    const heroSection = document.getElementById('hero');
    if (document.hidden) {
        heroSection.style.animationPlayState = 'paused';
    } else {
        heroSection.style.animationPlayState = 'running';
    }
});