// ========== FUNCIONALIDADES PRINCIPALES ==========

// 1. SISTEMA DE FILTROS DE PRODUCTOS
document.addEventListener('DOMContentLoaded', function() {
    const filtros = document.querySelectorAll('.filtro-btn');
    const productos = document.querySelectorAll('.producto-card');
    
    filtros.forEach(filtro => {
        filtro.addEventListener('click', function() {
            // Cambiar filtro activo
            filtros.forEach(f => f.classList.remove('activo'));
            this.classList.add('activo');
            
            const categoria = this.dataset.filtro;
            
            // Filtrar productos con animación
            productos.forEach(producto => {
                if (categoria === 'todos' || producto.dataset.categoria === categoria) {
                    producto.style.display = 'block';
                    producto.style.animation = 'fadeInUp 0.5s ease';
                } else {
                    producto.style.display = 'none';
                }
            });
        });
    });
});

// 2. SISTEMA DE COTIZACIÓN VÍA WHATSAPP
function inicializarCotizaciones() {
    const botonesCotizar = document.querySelectorAll('.boton-cotizar, .boton-producto-cotizar');
    
    botonesCotizar.forEach(boton => {
        boton.addEventListener('click', function() {
            const producto = this.dataset.producto;
            const specs = this.dataset.especificaciones;
            const whatsapp = this.dataset.whatsapp;
            
            const mensaje = `Hola! Me interesa el producto: ${producto}%0A%0AEspecificaciones: ${specs}%0A%0A¿Podrían enviarme una cotización?`;
            const urlWhatsapp = `https://wa.me/${whatsapp}?text=${mensaje}`;
            
            window.open(urlWhatsapp, '_blank');
        });
    });
}

// 3. BOTÓN "VER MÁS PRODUCTOS" 
document.getElementById('btnVerMasProductos')?.addEventListener('click', function() {
    // Mostrar productos ocultos
    const productosOcultos = document.querySelectorAll('.producto-card[style*="display: none"], .producto-card.oculto');
    
    this.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Cargando...';
    
    setTimeout(() => {
        // Mostrar siguiente lote (ej: 4 más)
        for(let i = 0; i < Math.min(4, productosOcultos.length); i++) {
            productosOcultos[i].style.display = 'block';
            productosOcultos[i].classList.remove('oculto');
            productosOcultos[i].style.animation = 'fadeInUp 0.5s ease';
        }
        
        // Si no quedan más productos, ocultar botón
        const restantes = document.querySelectorAll('.producto-card[style*="display: none"], .producto-card.oculto');
        if(restantes.length === 0) {
            this.style.display = 'none';
        } else {
            this.innerHTML = '<i class="fas fa-plus-circle"></i> Ver Más Productos';
        }
    }, 1000);
});

// 4. ANIMACIONES AL HACER SCROLL
function animarElementos() {
    const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.style.animation = 'fadeInUp 0.6s ease forwards';
            }
        });
    });

    document.querySelectorAll('.producto-card, .producto-destacado').forEach(el => {
        observer.observe(el);
    });
}

// 5. EFECTOS HOVER MEJORADOS
function efectosHover() {
    document.querySelectorAll('.producto-card, .producto-destacado').forEach(card => {
        card.addEventListener('mouseenter', function() {
            this.style.transform = 'translateY(-10px) scale(1.02)';
        });
        
        card.addEventListener('mouseleave', function() {
            this.style.transform = 'translateY(0) scale(1)';
        });
    });
}

// INICIALIZAR TODO
document.addEventListener('DOMContentLoaded', function() {
    inicializarCotizaciones();
    animarElementos();
    efectosHover();
    
    console.log('✅ Sistema de productos inicializado');
});

