/**
 * ================================
 * NOSOTROS.JS - BOMBAS AYALA
 * JavaScript completo para página Nosotros
 * ================================ */

document.addEventListener('DOMContentLoaded', function() {
    
    // ==================
    // 1. CONTADORES ANIMADOS
    // ==================
    
    const contadores = document.querySelectorAll('.numero-estadistica');
    
    const animarContador = (elemento) => {
        const objetivo = parseInt(elemento.getAttribute('data-numero'));
        const duracion = 2000; // 2 segundos
        const incremento = objetivo / (duracion / 16); // 60 FPS
        let actual = 0;
        
        const timer = setInterval(() => {
            actual += incremento;
            if (actual >= objetivo) {
                elemento.textContent = objetivo.toLocaleString();
                clearInterval(timer);
            } else {
                elemento.textContent = Math.floor(actual).toLocaleString();
            }
        }, 16);
    };
    
    // Observer para contadores
    const observerContadores = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                animarContador(entry.target);
                observerContadores.unobserve(entry.target);
            }
        });
    }, { threshold: 0.5 });
    
    contadores.forEach(contador => {
        observerContadores.observe(contador);
    });

    // ==================
    // 2. MAPA INTERACTIVO - LIMA METROPOLITANA (4 ZONAS)
    // ==================
    
    const zonas = {
        norte: {
            distritos: [
                'Ancón', 'Carabayllo', 'Comas', 'Independencia', 
                'Los Olivos', 'Puente Piedra', 'San Martín de Porres', 'Santa Rosa'
            ],
            color: 'var(--cian-principal)',
            descripcion: 'Lima Norte - 8 distritos'
        },
        sur: {
            distritos: [
                'Chorrillos', 'Lurín', 'Pachacámac', 'Pucusana', 
                'San Juan de Miraflores', 'Villa El Salvador', 'Villa María del Triunfo'
            ],
            color: 'var(--verde-esmeralda)',
            descripcion: 'Lima Sur - 7 distritos'
        },
        este: {
            distritos: [
                'Ate', 'Chaclacayo', 'Cieneguilla', 'El Agustino', 
                'La Molina', 'Lurigancho-Chosica', 'San Juan de Lurigancho', 'Santa Anita'
            ],
            color: 'var(--naranja-tecnico)',
            descripcion: 'Lima Este - 8 distritos'
        },
        oeste: {
            distritos: [
                'Barranco', 'Breña', 'Callao', 'Jesús María', 'La Victoria', 
                'Lima Cercado', 'Lince', 'Magdalena del Mar', 'Miraflores', 
                'Pueblo Libre', 'Rímac', 'San Borja', 'San Isidro', 'San Luis', 
                'San Miguel', 'Santiago de Surco', 'Surquillo'
            ],
            color: 'var(--azul-claro)',
            descripcion: 'Lima Oeste - 17 distritos'
        }
    };

    // Función para mostrar distritos
    const mostrarDistritos = (tipoZona) => {
        const listaDistritos = document.querySelector('.lista-distritos');
        const zona = zonas[tipoZona];
        
        if (!listaDistritos || !zona) return;
        
        listaDistritos.innerHTML = `
            <div class="zona-titulo" style="color: ${zona.color}; font-weight: 700;">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                    <circle cx="12" cy="10" r="3"/>
                    <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                </svg>
                ${zona.descripcion}
            </div>
            <div class="distritos-grid">
                ${zona.distritos.map(distrito => `
                    <div class="distrito-item" style="color: ${zona.color}; font-weight: 600;">
                        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                            <circle cx="12" cy="10" r="3"/>
                            <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                        </svg>
                        ${distrito}
                    </div>
                `).join('')}
            </div>
        `;
        
        // Animar entrada de distritos
        const items = listaDistritos.querySelectorAll('.distrito-item');
        items.forEach((item, index) => {
            item.style.opacity = '0';
            item.style.transform = 'translateX(-20px)';
            setTimeout(() => {
                item.style.transition = 'all 0.3s ease';
                item.style.opacity = '1';
                item.style.transform = 'translateX(0)';
            }, index * 50);
        });
    };

    // Event listeners para zonas del mapa (4 zonas)
    const zonasElementos = document.querySelectorAll('.zona-norte, .zona-sur, .zona-este, .zona-oeste');
    
    zonasElementos.forEach(zona => {
        zona.addEventListener('mouseenter', function() {
            const tipoZona = this.classList.contains('zona-norte') ? 'norte' : 
                            this.classList.contains('zona-sur') ? 'sur' :
                            this.classList.contains('zona-este') ? 'este' : 'oeste';
            
            mostrarDistritos(tipoZona);
        });
        
        // Efecto visual en hover
        zona.addEventListener('mouseover', function() {
            this.style.transform = 'scale(1.05)';
            this.style.zIndex = '10';
        });
        
        zona.addEventListener('mouseout', function() {
            this.style.transform = 'scale(1)';
            this.style.zIndex = '1';
        });
    });

    // ==================
    // 3. ANIMACIÓN SCROLL REVEAL
    // ==================
    
    const elementosAnimados = document.querySelectorAll('.estadistica-item, .mision-section, .vision-section, .valor-item');
    
    const observerAnimacion = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.style.opacity = '1';
                entry.target.style.transform = 'translateY(0)';
                entry.target.style.transition = 'all 0.6s ease-out';
                observerAnimacion.unobserve(entry.target);
            }
        });
    }, { 
        threshold: 0.1,
        rootMargin: '0px 0px -50px 0px'
    });
    
    elementosAnimados.forEach(elemento => {
        elemento.style.opacity = '0';
        elemento.style.transform = 'translateY(30px)';
        observerAnimacion.observe(elemento);
    });

    // ==================
    // 4. VIDEO OVERLAY INTERACTION
    // ==================
    
    const overlayVideo = document.querySelector('.overlay-imagen');
    const iconoPlay = document.querySelector('.icono-play');
    
    if (overlayVideo && iconoPlay) {
        overlayVideo.addEventListener('click', function() {
            // Aquí puedes agregar la lógica para abrir un modal con video
            console.log('Reproducir video de la empresa');
            
            // Ejemplo de animación del icono
            iconoPlay.style.transform = 'scale(1.2)';
            setTimeout(() => {
                iconoPlay.style.transform = 'scale(1)';
            }, 200);
            
            // Placeholder para integración con modal o lightbox
            alert('Video de presentación de Bombas Ayala\n(Integrar con tu reproductor preferido)');
        });
    }

    // ==================
    // 5. ESTADÍSTICAS DINÁMICAS
    // ==================
    
    const estadisticas = {
        experiencia: 25,
        clientes: 5000,
        servicios: 15000,
        atencion: 24
    };
    
    // Función para actualizar estadísticas (útil para datos dinámicos)
    const actualizarEstadisticas = (nuevasEstadisticas) => {
        Object.keys(nuevasEstadisticas).forEach(key => {
            const elemento = document.querySelector(`[data-numero="${estadisticas[key]}"]`);
            if (elemento) {
                elemento.setAttribute('data-numero', nuevasEstadisticas[key]);
                estadisticas[key] = nuevasEstadisticas[key];
            }
        });
    };

    // ==================
    // 6. SMOOTH SCROLL PARA ENLACES INTERNOS
    // ==================
    
    const enlacesInternos = document.querySelectorAll('a[href^="#"]');
    
    enlacesInternos.forEach(enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            const targetId = this.getAttribute('href');
            const targetElement = document.querySelector(targetId);
            
            if (targetElement) {
                targetElement.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        });
    });

    // ==================
    // 7. LAZY LOADING PARA IMÁGENES
    // ==================
    
    const imagenes = document.querySelectorAll('img[data-src]');
    
    const observerImagenes = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                const img = entry.target;
                img.src = img.getAttribute('data-src');
                img.removeAttribute('data-src');
                observerImagenes.unobserve(img);
            }
        });
    });
    
    imagenes.forEach(img => {
        observerImagenes.observe(img);
    });

    // ==================
    // 8. BOTONES CTA CON TRACKING
    // ==================
    
    const botonesCTA = document.querySelectorAll('.boton-cta');
    
    botonesCTA.forEach(boton => {
        boton.addEventListener('click', function() {
            const accion = this.textContent.trim();
            const tipo = this.classList.contains('primario') ? 'primario' : 'secundario';
            
            // Analytics tracking (opcional)
            if (typeof gtag !== 'undefined') {
                gtag('event', 'cta_click', {
                    'event_category': 'nosotros',
                    'event_label': accion,
                    'value': tipo
                });
            }
            
            console.log(`CTA clicked: ${accion} (${tipo})`);
        });
    });

    // ==================
    // 9. DATOS INFORMATIVOS DE LIMA (4 ZONAS)
    // ==================
    
    const datosLima = {
        totalDistritos: Object.values(zonas).reduce((total, zona) => total + zona.distritos.length, 0),
        poblacionAproximada: '10.5 millones',
        areaCobertura: '100%',
        tiempoRespuestaPromedio: '20 minutos',
        zonasCobertura: 4
    };
    
    // Mostrar información adicional si es necesario
    const mostrarInfoLima = () => {
        console.log('Cobertura Bombas Ayala:', datosLima);
    };

    // ==================
    // 10. INICIALIZACIÓN Y MENSAJE DE BIENVENIDA
    // ==================
    
    // Mostrar información inicial en la lista de distritos
    const listaDistritos = document.querySelector('.lista-distritos');
    if (listaDistritos && listaDistritos.innerHTML.trim() === '') {
        listaDistritos.innerHTML = `
            <div class="zona-titulo" style="color: var(--azul-corporativo); font-weight: 700;">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                    <circle cx="12" cy="10" r="3"/>
                    <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                </svg>
                Cobertura en Lima Metropolitana (4 Zonas)
            </div>
            <div class="distritos-grid">
                <div class="distrito-item" style="color: var(--gris-medio);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="12" r="10"/>
                        <line x1="12" y1="16" x2="12" y2="12"/>
                        <line x1="12" y1="8" x2="12.01" y2="8"/>
                    </svg>
                    Pasa el cursor sobre una zona para ver los distritos
                </div>
                <div class="distrito-item" style="color: var(--azul-corporativo);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="10" r="3"/>
                        <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                    </svg>
                    ${datosLima.totalDistritos} distritos en 4 zonas
                </div>
                <div class="distrito-item" style="color: var(--verde-esmeralda);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="12" r="10"/>
                        <polyline points="12,6 12,12 16,14"/>
                    </svg>
                    Tiempo respuesta: ${datosLima.tiempoRespuestaPromedio}
                </div>
            </div>
        `;
    }
    
    // Prevenir flicker y mostrar contenido
    document.body.style.opacity = '1';
    
    // Log de inicialización (desarrollo)
    console.log('🚰 Bombas Ayala - Página Nosotros iniciada');
    console.log('📍 Zonas cubiertas: Norte, Sur, Este, Oeste (4 zonas)');
    console.log('📍 Distritos cubiertos:', datosLima.totalDistritos);
    console.log('⏱️ Scripts cargados correctamente');

    // ==================
    // 11. FUNCIONES PÚBLICAS (OPCIONAL)
    // ==================
    
    // Exponer funciones útiles al scope global si es necesario
    window.BombasAyala = {
        mostrarDistritos,
        actualizarEstadisticas,
        datosLima,
        zonas
    };
    
});