// ========================================
// JAVASCRIPT - SECCIÓN DE CONTACTO CORREGIDO
// ========================================

document.addEventListener('DOMContentLoaded', function() {
    
    // ========================================
    // FUNCIONALIDAD DEL MAPA INTERACTIVO
    // ========================================
    
    const mapaInteractivo = document.getElementById('mapaInteractivo');
    const informacionMapa = document.getElementById('informacionMapa');
    const puntoUbicacion = document.getElementById('puntoUbicacion');
    
    // Coordenadas exactas (Ubicación específica)
    const coordenadasExactas = {
        lat: -12.113154,
        lng: -77.050290
    };
    
    // Hover en el mapa - ocultar información
    if (mapaInteractivo && informacionMapa) {
        mapaInteractivo.addEventListener('mouseenter', function() {
            informacionMapa.style.opacity = '0';
            informacionMapa.style.transform = 'translateY(-10px)';
        });
        
        mapaInteractivo.addEventListener('mouseleave', function() {
            informacionMapa.style.opacity = '1';
            informacionMapa.style.transform = 'translateY(0)';
        });
    }
    
    // ========================================
    // FUNCIONALIDAD DEL FORMULARIO
    // ========================================
    
    // ✅ CORREGIDO: Usar el ID correcto del HTML
    const formularioContacto = document.getElementById('formularioContacto');
    const botonEnviar = document.querySelector('.boton-enviar');
    
    if (formularioContacto) {
        
        // Validación en tiempo real
        const inputs = formularioContacto.querySelectorAll('input, select, textarea');
        
        inputs.forEach(input => {
            input.addEventListener('blur', function() {
                validarCampo(this);
            });
            
            input.addEventListener('input', function() {
                if (this.classList.contains('error')) {
                    validarCampo(this);
                }
            });
        });
        
        // Función de validación individual
        function validarCampo(campo) {
            const valor = campo.value.trim();
            const esRequerido = campo.hasAttribute('required');
            let esValido = true;
            
            // Remover clases de error previas
            campo.classList.remove('error', 'valido');
            
            // Validar campos requeridos
            if (esRequerido && valor === '') {
                esValido = false;
            }
            
            // Validar email
            if (campo.type === 'email' && valor !== '' && !validarEmail(valor)) {
                esValido = false;
            }
            
            // Validar teléfono
            if (campo.type === 'tel' && valor !== '' && !validarTelefono(valor)) {
                esValido = false;
            }
            
            // Aplicar clases
            if (esValido && valor !== '') {
                campo.classList.add('valido');
            } else if (!esValido) {
                campo.classList.add('error');
            }
            
            return esValido;
        }
        
        // Validar email
        function validarEmail(email) {
            const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            return regex.test(email);
        }
        
        // Validar teléfono
        function validarTelefono(telefono) {
            const regex = /^[\d\s\-\+\(\)]{7,15}$/;
            return regex.test(telefono);
        }
        
        // ✅ CORREGIDO: Envío del formulario
        formularioContacto.addEventListener('submit', function(e) {
            e.preventDefault();
            
            // Validar todos los campos
            let formularioValido = true;
            inputs.forEach(input => {
                if (!validarCampo(input)) {
                    formularioValido = false;
                }
            });
            
            if (formularioValido) {
                enviarFormulario();
            } else {
                mostrarMensaje('Por favor, corrige los errores en el formulario.', 'error');
                // Scroll al primer error
                const primerError = formularioContacto.querySelector('.error');
                if (primerError) {
                    primerError.focus();
                    primerError.scrollIntoView({ behavior: 'smooth', block: 'center' });
                }
            }
        });
        
        // Función para enviar formulario
        function enviarFormulario() {
            // Cambiar estado del botón
            const textoOriginal = botonEnviar.innerHTML;
            botonEnviar.innerHTML = '<i class="fas fa-spinner fa-spin"></i> ENVIANDO...';
            botonEnviar.disabled = true;
            
            // Obtener datos del formulario
            const datosFormulario = new FormData(formularioContacto);
            const datos = Object.fromEntries(datosFormulario);
            
            // Simular envío (aquí integrarías con tu backend)
            setTimeout(() => {
                console.log('Datos del formulario:', datos);
                
                // Mostrar mensaje de éxito
                mostrarMensaje('¡Mensaje enviado correctamente! Nos pondremos en contacto contigo pronto.', 'exito');
                
                // Limpiar formulario
                formularioContacto.reset();
                inputs.forEach(input => {
                    input.classList.remove('error', 'valido');
                });
                
                // Restaurar botón
                botonEnviar.innerHTML = textoOriginal;
                botonEnviar.disabled = false;
                
            }, 2000);
        }
        
        // Mostrar mensajes
        function mostrarMensaje(mensaje, tipo) {
            // Remover mensaje anterior si existe
            const mensajeAnterior = document.querySelector('.mensaje-formulario');
            if (mensajeAnterior) {
                mensajeAnterior.remove();
            }
            
            // Crear nuevo mensaje
            const divMensaje = document.createElement('div');
            divMensaje.className = `mensaje-formulario ${tipo}`;
            divMensaje.innerHTML = `
                <i class="fas ${tipo === 'exito' ? 'fa-check-circle' : 'fa-exclamation-triangle'}"></i>
                <span>${mensaje}</span>
            `;
            
            // Insertar mensaje
            formularioContacto.insertBefore(divMensaje, formularioContacto.firstChild);
            
            // Auto-remover después de 5 segundos
            setTimeout(() => {
                if (divMensaje) {
                    divMensaje.style.opacity = '0';
                    setTimeout(() => divMensaje.remove(), 300);
                }
            }, 5000);
        }
    }
    
    // ========================================
    // ANIMACIONES Y EFECTOS
    // ========================================
    
    // ✅ CORREGIDO: Animación de entrada para elementos
    const observerOptions = {
        threshold: 0.1,
        rootMargin: '0px 0px -50px 0px'
    };
    
    const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.classList.add('animado');
            }
        });
    }, observerOptions);
    
    // ✅ CORREGIDO: Observar elementos para animación (usando clases correctas)
    const elementosAnimados = document.querySelectorAll('.seccion-formulario-contacto, .informacion-contacto-directo, .seccion-horarios');
    elementosAnimados.forEach(el => {
        if (el) observer.observe(el);
    });
    
    // ✅ CORREGIDO: Animación simple al hacer scroll
    window.addEventListener('scroll', function() {
        const seccionContacto = document.querySelector('.seccion-contacto');
        if (seccionContacto) {
            const rect = seccionContacto.getBoundingClientRect();
            
            if (rect.top < window.innerHeight && rect.bottom > 0) {
                seccionContacto.classList.add('visible');
            }
        }
    });
    
    // ========================================
    // FUNCIONES DE UTILIDAD
    // ========================================
    
    // Smooth scroll para links internos
    document.querySelectorAll('a[href^="#"]').forEach(link => {
        link.addEventListener('click', function(e) {
            e.preventDefault();
            const target = document.querySelector(this.getAttribute('href'));
            if (target) {
                target.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start'
                });
            }
        });
    });
    
    // Copiar información de contacto al hacer clic
    document.querySelectorAll('.detalles-contacto span').forEach(span => {
        span.addEventListener('click', function() {
            const texto = this.textContent;
            if (navigator.clipboard) {
                navigator.clipboard.writeText(texto).then(() => {
                    // Mostrar feedback visual
                    const original = this.textContent;
                    this.textContent = '¡Copiado!';
                    this.style.color = '#27AE60'; // Verde esmeralda
                    
                    setTimeout(() => {
                        this.textContent = original;
                        this.style.color = '';
                    }, 1000);
                }).catch(() => {
                    console.log('No se pudo copiar el texto');
                });
            }
        });
    });
    
    // Preloader para el mapa
    if (mapaInteractivo) {
        const iframe = mapaInteractivo.querySelector('iframe');
        if (iframe) {
            iframe.addEventListener('load', function() {
                mapaInteractivo.classList.add('mapa-cargado');
            });
        }
    }
    
    console.log('Sistema de contacto inicializado correctamente');
});

// ========================================
// FUNCIONES ADICIONALES CORREGIDAS
// ========================================

