function inicializarMapaPersonalizado() {
    const mapaInteractivo = document.getElementById('mapaInteractivo');
    const informacionMapa = document.getElementById('informacionMapa');
    
    if (mapaInteractivo && informacionMapa) {
        // Efectos adicionales del mapa
        mapaInteractivo.addEventListener('click', function() {
            // Aquí puedes agregar funcionalidad de clic en el mapa
            console.log('Mapa clickeado');
        });
    }
}

// Inicializar mapa personalizado cuando el DOM esté listo
document.addEventListener('DOMContentLoaded', inicializarMapaPersonalizado);